body{
    margin: 0;
}
.headers{
   
    display: flex;
    justify-content: space-between;
    color: white;
    background: black;
    padding-left: 20px;
    padding-right: 20px;
}
.light{
margin-top: 30px;
height: 20px;
}
.SearchContainer{
display: flex;
margin-top: 40px;
  

}
.Searchicon-Container{

    height: 40px;
    
    display: flex;
    justify-content: center;
    margin: 12px;
}
.searchInput{
   width: 300px;
    padding-left: 20px;
}
.searchContainerBox{
    display: flex;
    justify-content: space-between;
    margin: 20px;
       flex-wrap: wrap;
      
}
.select{
margin-top: 40px;
height: 70px;
width: 300px;
margin-left: 120px;
}
.bookmaindiv{
   display: flex;
   justify-content: space-evenly;
    flex-wrap: wrap;
    margin-top: 50px;
}

.carddiv{
   border: 2px solid black;
   display: flex;
    margin: 20px;
    height: 300px;
    justify-content: flex-start;
    align-items: start;
    box-shadow: 10px 2px 20px;
    min-width: 300px;
}
.bookmaindiv :hover{

  transform: scale(0.99);
  cursor: pointer;

}
a{
height: 30px;
   text-decoration: none;
   color: black;
}

